/**
    * echarts转换引擎
    */

import { CommConfig } from './comm'
// import logoSvg from '../../assets/logo.svg'

const echartsEngine = function (chartOptions) {
    //TODO: chartOptions 转换后导出

    const chartAllTypeArray = chartOptions.chartAllType.split('|');
    // const chartPro = chartAllTypeArray[0];
    const chartType = chartAllTypeArray[1];
    // const chartStyle = chartAllTypeArray[2];

    const commConfig = new CommConfig();
    
    const option = {
        color: commConfig.get_color().colorWheelMultiple,
        grid: commConfig.get_grid(),
        title: commConfig.get_title(),
        tooltip: commConfig.get_tooltips(),
        legend: commConfig.get_legend(),
        xAxis: commConfig.get_xAxis(),
        yAxis: commConfig.get_yAxis(),
        // graphic: [
        //     {
        //         type: 'image',
        //         id: 'logo',
        //         right: '1%',
        //         bottom: '1%',
        //         z: 0,
        //         bounding: 'all',
        //         style: {
        //             image: logoSvg,
        //             width: 134 * 0.6,
        //             height: 25 * 0.6,
        //             opacity: 0.2
        //         },
        //     }
        // ],
        series: chartOptions.defaultOption.series ? chartOptions.defaultOption.series : [{
            name: '--',
            type: 'bar',
            data: []
        }]
    };
    
    const xAxisData = chartOptions.chartDataCache.xAxis;
    option.xAxis.data = xAxisData;

    // 条形图交换XY轴
    if (chartType === 'bar') {
        option.grid.right = '5%';
        let x = option.xAxis;
        let y = option.yAxis;
        option.xAxis = y;
        option.yAxis = x;
    }

    // 饼图去掉XY轴
    if(chartType == 'pie'){
        delete option.xAxis
        delete option.yAxis
    }

    // console.dir(option)
    // console.dir(JSON.stringify(option))
    return option;
}

export default echartsEngine