  mounted() {
    if (this.lang == "zh") {
      this.config[0].data = this.echartsCN
      this.close = "关闭图表选择菜单";
      this.chooseConfig();
      return;
    }
    this.config[0].data = this.echartsEN
    this.close = "close chart-select menu";
    this.chooseConfig();
  },
  computed: {
    ...mapState("chartSetting", ["chartLists", "currentChartIndex"]),

    chartPro() {
      return this.chartAllType.split("|")[0];
    },
    chartType() {
      return this.chartAllType.split("|")[1];
    },
    chartStyle() {
      return this.chartAllType.split("|")[2];
    }
  },
  watch: {
    currentChartType(val) {
      this.chartAllType = val;
    },
    lang() {
      if (this.lang == "zh") {
        this.config[0].data = this.echartsCN
        this.close = "关闭图表选择菜单";
        this.chooseConfig();
        return;
      }
      this.config[0].data = this.echartsEN
      this.close = "close chart-select menu";
      this.chooseConfig();
    },
  },
  methods: {
    chooseConfig() {
      for (var i = 0; i < this.config.length; i++) {
        if (this.config[i].type == this.chartPro) {
          this.currentConfig = this.config[i];
          return
        }
      }
      this.currentConfig = this.config[0];
    },
    changeChartType: function (value) {
      // 相同类型或者currentChartIndex为null不执行
      if (this.currentChartIndex == null) {
        return null;
      }
      if (
        (this.chartLists[this.currentChartIndex].chartOptions,
        chartreuse == value)
      ) {
        return null;
      }
      changeChangeAllType(
        deepCopy(this.chartLists[this.currentChartIndex].chartOptions),
        value
      );
      this.$emit('closeChartShowList')
      this.currentType = value;
    },
    quickListScroll: function (e) {
      var scrollTop = $(e.currentTarget).scrollTop();
      var _this = this;
      $(e.currentTarget)
        .find("div.luckysheet-datavisual-quick-list-title")
        .each(function () {
          var $t = $(this),
            position = $t.position();
          //console.log($t.find("a").data("type"),position.top, scrollTop);
          if (scrollTop >= _this.list_scroll_direction) {
            if (position.top + 55 > 0) {
              $("#luckysheet-datavisual-quick-menu div").removeClass(
                "luckysheet-datavisual-quick-menu-active"
              );
              $(
                "#luckysheet-datavisual-chart-menu-" + $t.find("a").data("type")
              ).addClass("luckysheet-datavisual-quick-menu-active");
              return false;
            }
          } else {
            if (position.top - 55 >= 0) {
              $("#luckysheet-datavisual-quick-menu div").removeClass(
                "luckysheet-datavisual-quick-menu-active"
              );
              var $obj = $t.prev().prev();
              if ($obj.length == 0) {
                $obj = $t;
              }
              $(
                "#luckysheet-datavisual-chart-menu-" +
                  $obj.find("a").data("type")
              ).addClass("luckysheet-datavisual-quick-menu-active");
              return false;
            }
          }
          setTimeout(function () {
            _this.list_scroll_direction = scrollTop;
          }, 0);
        });
    },
    quickMenu: function (e) {
      var $t = $(e.currentTarget);
      $("#luckysheet-datavisual-quick-menu div").removeClass(
        "luckysheet-datavisual-quick-menu-active"
      );
      $t.addClass("luckysheet-datavisual-quick-menu-active");
      var $scroll = $(
          "#luckysheet-datavisual-chart-listtitle-" + $t.data("type")
        ).parent(),
        p = $scroll.position().top;
      $("#luckysheet-datavisual-quick-list").scrollTop(
        p + 5 + $("#luckysheet-datavisual-quick-list").scrollTop()
      );
    },
  },
};
</script>

